const app = getApp()
export default {
	data() {
		return {
			messageList: [
				{title: '请输入编号获取链接',status: '0'}
			],
			userImg: '',
			inputValue: ''
		}
	},
	onLoad() {
		this.userImg = app.globalData.userInfo.avatar
	},
	methods: {
		getSetData(e){
			console.log(e)
			this.inputVal = e.detail.value;
			this.inputValue = this.inputVal
		},
		send(){
			console.log(this.inputVal)
			if(this.inputValue == ''){
				uni.showToast({
					title: '请输入内容',
					icon: 'none'
				})
				return false
			}else{
				this.item = {
					title: this.inputVal,
					status: '1'
				}
				this.messageList.push(this.item)
				this.inputValue = ''
				setTimeout(e => {
					uni.request({
						url: `${this.$helper.requestUrl}customer/getDataUrl`,
						method: 'POST',
						header: {
							authorization: app.globalData.token
						},
						data:{
							ordernum: this.inputVal
						},
						success: res => {
							res = this.$helper.null2str(res.data);
							console.log(res, '-------------------');
							if (res.status_code == 'ok') {
								this.list = {
									title: res.data,
									status: '0'
								}
								this.messageList.push(this.list)
								// this.getPay(res.data);
							} else {
								this.list = {
									title: res.message,
									status: '0'
								}
								this.messageList.push(this.list)
							}
						}
					});
				},1000)
			}
			
		},
		copy(e){
			uni.setClipboardData({
				data: e,
				success: function (res) {
					uni.showToast({
						title: '复制成功',
						icon: 'none'
					})
				}
			});
		}
		
	}
}
